.dashBoard{
    color:rgb(56, 56, 56);
    margin-top: 20px;
    font-size: 14px;
    border: 1px solid rgb(196, 196, 196);
    padding: 5px;
    width: 200px;
    font-family: Arial, Helvetica, sans-serif;
    .user-photo{
        width: 100%;
        margin: auto;
    
        
        .img{
            margin: auto;
            width: 66px;
            text-align: center;
            border: 1px solid #c4c4c4;
            border-radius: 50%;
            height: 60px;
            align-items: center;
            background-image: url("../img/placeholder.png");
        }
    }
    .view{
        background-color: rgb(94, 94, 94);
        color: white;
    }
    
}